.first-screen {
    width: 100%;
    height: 100vh;
    position: relative;
    padding: 220px 0 50px 0;
    @media (max-width: $md-3) {
        padding: 0 0 50px 0;
    }
    @media (max-width: $md-4) {
        padding: 80px 0 50px 0;
    }
    @media (max-width: $md-6) {
        height: auto;
    }
   
    // .first-screen__body

    &__body {
        width: 100%;
        height: 100%;
        color: #fff;
        @media (max-width: $md-3) {
            display: flex;
            flex-direction: column;
            justify-content: center;
        }
        @media (max-width: $md-6) {
            display: block;
        }
    }

    // .first-screen__top

    &__top {
        margin: 0 0 70px 0;
        display: flex;
        align-items: center;
        justify-content: space-between;
        @media (max-width: $md-4) {
            flex-direction: column;
            align-items: flex-start;
        }
        @media (max-width: $md-6) {
            margin: 0 0 30px 0;
        }
    }

    // .first-screen__title

    &__title {
        margin: 0 90px 0 0;
        font-size: 90px;
        max-width: 441px;
        @media (max-width: 1400px) {
            font-size: 70px;
            margin: 0 50px 0 0;
        } 
        @media (max-width: $md-3) {
            font-size: 55px;
            max-width: 150px;
        }
        @media (max-width: $md-4) {
            max-width: 100%;
            margin: 0 0 30px 0;
        }

    }

    // .first-screen__slider

    &__slider {
        width: 700px;
        display: flex;
        justify-content: flex-start;
        @media (max-width: 1400px) {
            width: 550px;
        } 
        @media (max-width: $md-3) {
            width: 450px;
        }
        @media (max-width: $md-4) {
            width: 100%;
        }
       
    }

    // .first-screen__item

    &__item {
        display: flex;
    }

    // .first-screen__image

    &__image {
        margin: 0 62px 0 0;
        @media (max-width: 1400px) {
            margin: 0 30px 0 0;
        } 
        @media (max-width: $md-3) {
            width: 250px;
            height: 250px;
        }
        @media (max-width: $md-4) {
            margin: 0 10px 0 0;
        }
        @media (max-width: $md-6) {
            width: 180px;
            height: 180px;
        }
       
    }

    // .first-screen__content

    &__content {
        display: flex;
        flex-direction: column;
    }

    // .first-screen__details

    &__details {
        font-size: 20px;
        margin: 0 0 10px 0;
        @media (max-width: $md-5) {
            font-size: 18px;
        }
    }

    // .first-screen__price

    &__price {
        font-weight: 500;
        font-size: 38px;
        margin: 0 0 20px 0;
        @media (max-width: $md-4) {
            font-size: 32px;
        }
        @media (max-width: $md-5) {
            font-size: 28px;
        }
    }

    // .first-screen__start

    &__start {
        display: flex;
        align-items: center;
        margin: 0 0 10px 0;

        div {
            @include bg("contain");
             width: 15px;
             height: 15px;
             margin: 0 2px;
        }
    }
    

    // .first-screen__size

    &__size {
        font-size: 18px;
        margin: 0 0 60px 0;
    }

    // .first-screen__arrows

    &__arrows {
        position: fixed;
        top: 200px;
        @media (max-width: $md-6) {
            top: 140px;
        }
    }

    // .first-screen__bottom

    &__bottom {
        display: flex;
        align-items: center;
        @media (max-width: 650px) {
            flex-direction: column;
            align-items: flex-start;
        }
    }

    // .first-screen__text

    &__text {
        font-size: 18px;
        max-width: 353px;
        margin: 0 92px 0 100px;
        position: relative;
        @media (max-width: $md-4) {
            margin: 0 40px 0 60px;
        } 
        @media (max-width: 650px) {
            margin: 0 0 30px 60px;
        }
        &::after {
            @media (max-width: $md-4) {
                width: 40px;
                left: -60px;
            } 
            position: absolute;
            top: 5px;
            left: -98px;
            width: 78px;
            height: 2px;
            display: block;
            content: "";
            background-color: #C4C4C4;
        }
    }

    // .first-screen__block

    &__block {
        display: flex;
        align-items: center;
    }

    // .first-screen__button

    &__button {
        @include no-btn();
        width: 160px;
        height: 58px;
        border-radius: 32px;
        background-color: $pinkColor;
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 0 25px 0 0 ;
        color: #fff;
        transition: .3s all 0s linear;
        &:hover, &:focus {
            box-shadow: 0 8px 10px 0 #61182296;
        }
        span {
            @include bg("contain");
            width: 10px;
            height: 10px;
            margin: 0 0 0 9px;
           
        }
    }

    // .first-screen__play

    &__play {
        width: 60px;
        height: 60px;
        @include bg("contain");
        cursor: pointer;
    }

    &__abst1, &__abst2, &__abst3,  &__bgtext {
        position: absolute;
        z-index: -50;
        top: 0;
    }

    &__abst1 {
        left: 0;
        width: 100%;
        height: 100%;
       
    }

    &__abst2 {
        right: 0;
        @media (max-width: 650px) {
            display: none;
         }
    }

    &__abst3 {
        left: 0;
        @media (max-width: 650px) {
            display: none;
         }
    }

    &__bgtext {
        top: 300px;
        z-index: -49;
        left: 700px;
        @media (max-width: 1600px) {
            left: 500px;
        }
        @media (max-width: 1400px) {
            display: none;
        }
    }
}

