.products {
    width: 100%;
    margin:  125px 0 10px 0;
    position: relative;
    @media (max-width: $md-4) {
        margin:  60px 0 10px 0;
    }
    // .products__body

    &__body {
        width: 100%;
        height: 100%;
        color: #fff;
    }

    // .products__top

    &__top {
        width: 100%;
        margin: 0 0 100px 0;
        @media (max-width: $md-4) {
            margin: 0 0 50px 0;
        }
    }

    // .products__title

    &__title {
        font-size: 56px;
        margin: 0 0 30px 0;
        @media (max-width: $md-5) {
            font-size: 46px;
        }
    }

    // .products__text

    &__text {
        color: $greyColor;
        max-width: 536px;
        @media (max-width: $md-4) {
            max-width: 400px;
        }
        @media (max-width: $md-6) {
            max-width: 320px;
        }
    }

    // .products__content

    &__content {
        display: flex;
        align-items: center;
        justify-content: space-between;
        @media (max-width: $md-3) {
            flex-direction: column;
            align-items: center;
            justify-content: center;
        }
    }

    // .products__item

    &__item {
        position: relative;
        width: 310px;
        height: 525px;
        margin: 0 10px;
        text-align: center;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 30px;
        border-radius: 8px;
        border: 1px solid rgba(0, 0, 0, 0);
        transition: .3s all 0s linear;
        @media (max-width: $md-3) {
            height: 350px;
            margin: 0 0 20px 0;
            width: 80%;
            background-color: #5f5b5b3b;
            border: 1px solid $greyColor;
        }
        @media (max-width: $md-5) {
            width: 100%;
        }
        &:hover {
            background-color: #5f5b5b3b;
            border: 1px solid $greyColor;
            .products__bg {
                display: block;
            }
        }
    }

    // .products__image

    &__image {
        margin-bottom: 40px;
        height: 156px;
        object-fit: contain;
    }

    // .products__caption

    &__caption {
        font-size: 20px;
        text-transform: uppercase;
        margin-bottom: 16px;
    }

    // .products__description

    &__description {
        min-width: 240px;
        font-size: 15px;
        color: $greyColor;
    }

    &__bg {
        display: none;
        position: absolute;
        top: 0;
        left: 0;
        z-index: -50;
        width: 100%;
        height: 70%;
        object-fit: cover;
        @media (max-width: $md-3) {
            display: block;
            height: 100%;
        }
        @media (max-width: $md-4) {
            top: -40px;
        }
    }

    &__light {
        position: absolute;
        top: -200px;
        left: 0;
        z-index: -50;
    }
}
