.header.fixed {
    position: fixed;
    top: 0;
    left: 0;
    background-color: #0a0304;
    animation-name: show_header;
    animation-duration: 1s;
    margin: 0;

    @keyframes show_header  {
        0% {
            opacity: 0;
            top: -50px;
        }
        100% {
            opacity: 1;
            top: 0;
        }
    }
}
.header {
    width: 100%;
    height: 58px;
    margin: 60px 0 0 0;
    position: absolute;
    z-index: 50;
    @media (max-width: $md-4) {
        height: 50px;
        margin: 20px 0 0 0;
    }
    // .header__container

    &__container {
        max-width: 1200px + 30px;
        height: 100%;
        padding: 0 15px;
        margin: 0 auto;
    }

    // .header__body

    &__body {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    // .header__logo

    &__logo {
        z-index: 3;
    }

    // .header__burger
    &__burger.active {
        &::after {
            transform: rotate(-45deg);
            top: 27px;
            @media (max-width: $md-4) {
                top: 5px;
            }
        }
        &::before {
            transform: rotate(45deg);
            bottom: 27px;
            @media (max-width: $md-4) {
                bottom: 5px;
            }
        }
    }
    &__burger {
        z-index: 3;
        cursor: pointer;
        width: 58px;
        height: 58px;
        border: 1.4px solid $greyColor;
        border-radius: 50%;
        position: relative;
        @media (max-width: $md-4) {
            border: none;
            width: 28px;
            height: 12px;
            margin: 0 10px 0 0;
        }
        &::after,
        &::before {
            position: absolute;
            left: 16px;
            display: block;
            content: "";
            width: 24px;
            height: 2px;
            background-color: #fff;
            transition: .2s all 0s linear;
            @media (max-width: $md-4) {
                width: 100%;
            }
            
        }
        &::after {
            top: 22px;
            @media (max-width: $md-4) {
                top: 0;
            }
        }
        &::before {
             bottom: 22px;
             @media (max-width: $md-4) {
                bottom: 0;
             }
        }
    }
    
    &__menu.active {
        right: 0;
    }
    &__menu {
        @media (max-width: $md-4) {
            z-index: 1;
            position: fixed;
            top: 0;
            right: -100%;
            width: 100%;
            height: 100%;
            background-color: #0a0304f6;
            padding: 120px 40px;
            transition: .4s all 0s linear;
        }
    }

    // .header__items

    &__items {
        display: flex;
	    align-items: flex-start;
        @media (max-width: $md-4) {
            flex-direction: column;
            align-items: center;
            padding: 0;
        }
    }

    // .header__item

    &__item {
        margin: 0 37px;
        @media (max-width: $md-3) {
            margin: 0 25px;
        }
        @media (max-width: $md-4) {
            margin: 15px 0;
        }
    }
}
