*,
*::before,
*::after {
	box-sizing: border-box;
}

.flex {
	display: flex;
	align-items: flex-start;
	justify-content: flex-start;
}

.--just-space {
	justify-content: space-between;
}

.--just-center {
	justify-content: center;
}

.--just-end {
	justify-content: flex-end;
}

.--align-str {
	align-items: stretch;
}

.--align-center {
	align-items: center;
}

.--align-end {
	align-items: flex-end;
}

.--dir-col {
	flex-direction: column;
}

html {
	font-size: 16px;
}

html,
body {
	min-height: 100%;
	position: relative;
}

body {
	font-size: 1rem;
}

ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
}

// Vars
$container: 1100px + 30px;
$md-1: $container;
$md-2: 1039.98px;
$md-3: 991.98px;
$md-4: 767.98px;
$md-5: 479.98px;
$md-6: 399.98px;

$mainColor: #3A1B22;
$pinkColor: #B2405D ;
$darkColor: #0A0304;
$greyColor: rgba(255, 255, 255, 0.1); 

// Elements
.container {
	max-width: $container;
	height: 100%;
	margin: 0 auto;
	padding: 0 15px;
}

.body {
	font-family: "Inter", sans-serif;
	background-color: #0A0304
}
.body.lock {
	overflow: hidden;
}
  
.logo {
	 @include bg("contain");
	 width: 85px;
	 height: 18px;
	 cursor: pointer;
}

.items {
	&__item {
		list-style: none;
		
		&_active {
			a {
				border-bottom: 2px solid  $pinkColor;
			}
		}
		a {
			cursor: pointer;
			text-decoration: none;
			color: #fff;
			text-transform: uppercase;
			font-weight: 500;
			font-size: 13px;
			padding-bottom: 8px;
			&:hover {	
				border-bottom: 2px solid  $pinkColor;
			}
		}
	}
}
 
.arrow {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 100px;
	height: 40px;

	&__left, &__right {
		width: 50%;
		height: 100%;
		border: 1px solid #392529;
		display: flex;
		align-items: center;
		justify-content: center;
		cursor: pointer;
		&:hover, &:focus {

		}
		span {
			width: 10px;
			height: 10px;
			@include bg("contain");
		}
	} 

    // .arrow__left
	
    &__left {
		border-bottom-left-radius: 32px;
		border-top-left-radius:32px;
		border-right: none;
		span {

		}
    }

    // .arrow__right

    &__right {
		border-bottom-right-radius: 32px;
		border-top-right-radius:32px;
		span {

		}
    }
	
}

