.service {
    width: 100%;
    margin-top: 100px;
    @media (max-width: $md-4) {
        margin-top: 60px;
    }

    // .service__body

    &__body {
        width: 100%;
        height: 100%;
        color: #fff;
    }

    // .service__top

    &__top {
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        margin: 0 0 115px 0;
        @media (max-width: $md-4) {
            margin: 0 0 55px 0;
        }
    }

    // .service__left

    &__left {
        margin: 0 20px 0 0;
    }

    // .service__subtitle

    &__subtitle {
         color: #FF678E;
         text-transform: uppercase;
         letter-spacing: 0.6em;
        margin-bottom: 15px;
        @media (max-width: $md-4) {
            font-size: 15px;
        }
    }

    // .service__title

    &__title {
        max-width: 446px;
        font-size: 60px;
        letter-spacing: 1px;
        @media (max-width: $md-4) {
            font-size: 50px;
        }
        @media (max-width: $md-5) {
            font-size: 40px;
        }
    }

    // .service__right

    &__right {
    }

    // .service__arrows

    &__arrows {
    }

    // .service__prev

    &__prev {
    }

    // .service__next

    &__next {
    }

    // .service__content

    &__content {
        max-width: 80%;
        position: relative;
        margin-right: 0;
        margin-left: auto;
        @media (max-width: $container) {
            max-width: 95%;
        }
    }

    // .service__slider

    &__slider {
        padding: 100px 0;
        @media (max-width: $md-4) {
            padding: 30px 0;
        }
        @media (max-width: $md-6) {
            padding: 0;
        }
    }

    // .service__item

    &__item {
        display: flex;
        position: relative;
        align-items: flex-end;
        margin: 0 20px 0 0;
    }

    // .service__number

    &__number {
        font-size: 50px;
        margin: 0 42px 0 0;
        @media (max-width: $md-2) {
            margin: 0 20px 0 0;
        }
        @media (max-width: $md-5) {
            display: none;
        }
    }

    // .service__image

    &__image {
        width: 232px;
        height: 240px;
        object-fit: contain;
        margin: 0 20px 0 0;
        @media (max-width: $md-4) {
            margin: 0 10px 0 0;
        }
        @media (max-width: $md-6) {
           width: 40%;
        }
    }

    // .service__image-bg

    &__image-bg {
        position: absolute;
        z-index: -50;
        width: 400px;
        height: 100%;
        left: 0;
        @media (max-width: $md-6) {
            width: 200px;
        }
    }

    // .service__block

    &__block {
    }

    // .service__caption

    &__caption {
        font-size: 30px;
        margin: 0 0 20px 0;
        @media (max-width: $md-5) {
            font-size: 28px;
        }
    }

    // .service__info

    &__info {
        max-width: 234px;
        margin: 0 0 48px 0;
        @media (max-width: $md-5) {
            font-size: 14px;
        }
    }

    // .service__link

    &__link {
        color: #fff;
        font-size: 14px;
        letter-spacing: 2px;
        &:hover {
            color: $pinkColor;
        }
    }
}
