.footer {
    width: 100%;
    background-color: #12070A;
    padding:  70px 0 45px 0;
    position: relative;
    margin-top: 80px;
    @media (max-width: $md-4) {
        padding: 30px 0;
        margin-top: 30px;
    }

    // .footer__container

    &__container {
    }

    // .footer__body

    &__body {
        width: 100%;
        height: 100%;
    }

    // .footer__top

    &__top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin: 0 0 60px 0;
        @media (max-width: $md-4) {
            margin: 0 0 20px 0;
        }
    }

    // .footer__logo

    &__logo {
    }

    // .footer__form

    &__form {
        width: 445px;
        height: 72px;
        border-radius: 50px;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: rgba(34, 16, 20, 0.4);
        @media (max-width: $md-4) {
            display: none;
        }
        &_hid {
            display: none;
            @media (max-width: $md-4) {
            display: block;
            margin: 0 0 30px 0;
              width: 100%;
              display: flex;
              flex-direction: column;
              align-items: center;
              background-color: rgba(34, 16, 20, 0.4);
              border-radius: 50px;
              height: auto;
              padding: 10px 10px;
            }
        }
       
    }

    // .footer__input

    &__input {
        background-color: rgba(0, 0, 0, 0);
        border: 1px solid transparent;
        color: #fff;
        outline: none;
        padding: 0 10px;
        @media (max-width: $md-4) {
            width: 100%;
            padding: 10px 40px;
            margin: 0 0 20px 0;
            text-align: center;
        }
    }
   

    // .footer__button

    &__button {
        
        width: 197px;
        height: 60px;
        border-radius: 32px;
        color: #fff;
        background-color: $pinkColor;
        display: flex;
        align-items: center;
        justify-content: flex-end;
        font-weight: 500;
        border: 1px solid $pinkColor;
        outline: none;
        cursor: pointer;
        transition: .4s all linear 0s;
        @media (max-width: $md-4) {
            width: 100%;
            justify-content: center;
        }
        &:hover {
            box-shadow: 0 8px 16px 0 #3a1b22b2;
        }
    }

    // .footer__button-arrow

    &__button-arrow {
        width: 50px;
        height: 50px;
        border-radius: 50%;
        background-color: #CB5B78;
        
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 0 0 0 25px ;
        span {
            @include bg("contain");
            width: 20px;
            height: 20px;
        }
    }

    // .footer__contacts

    &__contacts {
        display: flex;
        align-items: center;
    }

    // .footer__contact

    &__contact {
         width: 20px;
         height: 20px;
         @include bg("contain");
         margin: 0 10px;
         cursor: pointer;
         transition: .3s all linear 0s;
         
         &:hover {
            transform: translateY(2px);
         }
    }

    // .footer__bottom

    &__bottom {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    // .footer__copyright

    &__copyright, &__designed {
        font-size: 12px;
        color: rgba(255, 255, 255, 0.35);
        
    }

    &__copyright {
        margin: 0 10px 0 0;
    }

    // .footer__items

    &__items {
        display: flex;
        align-items: center;
        @media (max-width: $md-4) {
            display: none;
        }
    }

    // .footer__item

    &__item {
        margin: 0 37px;
        @media (max-width: $md-3) {
            margin: 0 15px;
        }
    }

    // .footer__designed

    &__designed {
        margin: 0 0 0 10px;
    }
}
