.feature {
    width: 100%;
    padding: 10px 0;
    position: relative;
    // .feature__body

    &__body {
        width: 100%;
        height: 100%;
        color: #fff;
    }

    // .feature__row

    &__row {
        display: flex;
        align-items: center;
        @media (max-width: 580px) {
            flex-direction: column;
            align-items: center;
        }
    }

  

    // .feature__item

    &__item {
        position: relative;
        height: 209px;
        display: flex;
        align-items: center;
        border: 1px solid $mainColor;
        padding: 0 20px;
        height: 150px;
        @media (max-width: $md-4) {
           font-size: 14px;
           justify-content: center;
        }
        @media (max-width: 580px) {
            height: 100px;
        } 
    }

    &__content {
        height: 56px;
    }

    // .feature__item-left

    &__item-left { 
        position: relative;
        justify-content: flex-start;
        width: 20%;
        border-right: 1px solid $pinkColor;
        border-left: none;
        @media (max-width: $md-4) {
            width: 25%;
        }
        @media (max-width: 580px) {
            width: 100%;
        }
    }

    // .feature__number

    &__number {
         color: #FF678E;
         font-weight: 500;
         margin: 0 0 8px 0;
    }

    // .feature__text

    &__text {
        font-style: 24px;
        font-weight: 500;
        letter-spacing: 1px;
        
    }

    // .feature__item-bg

    &__item-bg {
        position: absolute;
        z-index: -20;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }

    // .feature__item-shadow

    &__item-shadow {
        position: absolute;
        z-index: -19;
        width: 100%;
        height: 100%;
        bottom: 0;
        right: 0;
    }

    // .feature__item-center

    &__item-center {
       
        justify-content: center;
      
        &_1 {
            width: 30%;
        }
        &_2 {
            width: 25%;
        }
        &_1, &_2 {
            @media (max-width: $md-4) {
                width: 25%;
            }
            @media (max-width: 580px) {
                width: 100%;
                justify-content: flex-start;
            }
        }
    }

    // .feature__item-right

    &__item-right {
        border-right: none;
        justify-content: flex-end;
        width: 25%;
        @media (max-width: 580px) {
            width: 100%;
            justify-content: flex-start;
        }
    }

    &__line1, &__line2 {
        position: absolute;
        width: 100%;
        right: 0px;
    }

    &__line1 { 
        bottom: -2px;  
    }
    &__line2 {
        top: -2px;   
    }

    
}
